{"version":3,"file":"global.css","sources":["global.scss"],"sourcesContent":["/* Font Stuff */\n/*\n  400 regular\n  500 demi-bold\n  600 bold\n*/\n@font-face {\n  font-family: \"ManifoldCF\";\n  src: url(\"/assets/fonts/manifold/ManifoldCF-Regular.eot\"); /* IE9 Compat Mode */\n  src: url(\"/assets/fonts/manifold/ManifoldCF-Regular.eot?#iefix\")\n      format(\"embedded-opentype\"),\n    url(\"/assets/fonts/manifold/ManifoldCF-Regular.woff2\") format(\"woff2\"),\n    url(\"/assets/fonts/manifold/ManifoldCF-Regular.woff\") format(\"woff\"),\n    url(\"/assets/fonts/manifold/ManifoldCF-Regular.ttf\") format(\"truetype\"); /* Safari, Android, iOS */\n  font-weight: 400;\n  font-style: normal;\n}\n@font-face {\n  font-family: \"ManifoldCF\";\n  src: url(\"/assets/fonts/manifold/ManifoldCF-RegularOblique.eot\"); /* IE9 Compat Mode */\n  src: url(\"/assets/fonts/manifold/ManifoldCF-RegularOblique.eot?#iefix\")\n      format(\"embedded-opentype\"),\n    url(\"/assets/fonts/manifold/ManifoldCF-RegularOblique.woff2\")\n      format(\"woff2\"),\n    url(\"/assets/fonts/manifold/ManifoldCF-RegularOblique.woff\") format(\"woff\"),\n    url(\"/assets/fonts/manifold/ManifoldCF-RegularOblique.ttf\")\n      format(\"truetype\"); /* Safari, Android, iOS */\n  font-weight: 400;\n  font-style: italic;\n}\n@font-face {\n  font-family: \"ManifoldCF\";\n  src: url(\"/assets/fonts/manifold/ManifoldCF-DemiBold.eot\"); /* IE9 Compat Mode */\n  src: url(\"/assets/fonts/manifold/ManifoldCF-DemiBold.eot?#iefix\")\n      format(\"embedded-opentype\"),\n    url(\"/assets/fonts/manifold/ManifoldCF-DemiBold.woff2\") format(\"woff2\"),\n    url(\"/assets/fonts/manifold/ManifoldCF-DemiBold.woff\") format(\"woff\"),\n    url(\"/assets/fonts/manifold/ManifoldCF-DemiBold.ttf\") format(\"truetype\"); /* Safari, Android, iOS */\n  font-weight: 500;\n  font-style: normal;\n}\n@font-face {\n  font-family: \"ManifoldCF\";\n  src: url(\"/assets/fonts/manifold/ManifoldCF-DemiBoldOblique.eot\"); /* IE9 Compat Mode */\n  src: url(\"/assets/fonts/manifold/ManifoldCF-DemiBoldOblique.eot?#iefix\")\n      format(\"embedded-opentype\"),\n    url(\"/assets/fonts/manifold/ManifoldCF-DemiBoldOblique.woff2\")\n      format(\"woff2\"),\n    url(\"/assets/fonts/manifold/ManifoldCF-DemiBoldOblique.woff\") format(\"woff\"),\n    url(\"/assets/fonts/manifold/ManifoldCF-DemiBoldOblique.ttf\")\n      format(\"truetype\"); /* Safari, Android, iOS */\n  font-weight: 500;\n  font-style: italic;\n}\n@font-face {\n  font-family: \"ManifoldCF\";\n  src: url(\"/assets/fonts/manifold/ManifoldCF-Bold.eot\"); /* IE9 Compat Mode */\n  src: url(\"/assets/fonts/manifold/ManifoldCF-Bold.eot?#iefix\")\n      format(\"embedded-opentype\"),\n    url(\"/assets/fonts/manifold/ManifoldCF-Bold.woff2\") format(\"woff2\"),\n    url(\"/assets/fonts/manifold/ManifoldCF-Bold.woff\") format(\"woff\"),\n    url(\"/assets/fonts/manifold/ManifoldCF-Bold.ttf\") format(\"truetype\"); /* Safari, Android, iOS */\n  font-weight: 600;\n  font-style: normal;\n}\n@font-face {\n  font-family: \"ManifoldCF\";\n  src: url(\"/assets/fonts/manifold/ManifoldCF-BoldOblique.eot\"); /* IE9 Compat Mode */\n  src: url(\"/assets/fonts/manifold/ManifoldCF-BoldOblique.eot?#iefix\")\n      format(\"embedded-opentype\"),\n    url(\"/assets/fonts/manifold/ManifoldCF-BoldOblique.woff2\") format(\"woff2\"),\n    url(\"/assets/fonts/manifold/ManifoldCF-BoldOblique.woff\") format(\"woff\"),\n    url(\"/assets/fonts/manifold/ManifoldCF-BoldOblique.ttf\") format(\"truetype\"); /* Safari, Android, iOS */\n  font-weight: 600;\n  font-style: italic;\n}\n\n/* Colors */\n.kow-red,\na.kow-red,\n.type-block a.kow-red {\n  color: #ff0035;\n}\n.kow-green,\na.kow-green,\n.type-block a.kow-green {\n  color: #04aa56;\n}\n.kow-pink,\na.kow-pink,\n.type-block a.kow-pink {\n  color: #ff008d;\n}\n\n/* Grid stuff */\n\n/* Type blocks are areas where images and paragraphs interact, they sit next\nto each other and flow around one another. Most of the Layout rules will happen here. */\n* {\n  font-family: \"ManifoldCF\";\n  font-weight: 400;\n  color: #1b1b1b;\n  -webkit-font-smoothing: antialiased;\n  font-smoothing: antialiased;\n}\nhtml {\n  background-color: #eef3f4;\n  /*background-color: white;*/\n}\n.site-wrapper {\n  width: 100%;\n  max-width: 1240px;\n  margin: auto;\n  padding-top: 20px;\n}\n\n/* Article Header */\n.article-header {\n  display: block;\n  clear: both;\n  position: relative;\n  height: calc(50% - 60px);\n  max-height: 590px; /* We may need this again. */\n  min-height: 250px;\n  /*background-color:blue;*/\n}\n\n.article-header .text-block {\n  display: block;\n  position: absolute;\n  width: calc(50% - 60px);\n  bottom: 0;\n  right: 0;\n}\n\n.article-header .text-block p,\n.article-header .text-block span,\n.article-header .text-block h1 {\n  float: none;\n  text-align: left;\n  margin: 0;\n  margin-bottom: 0;\n}\n\n.article-header .text-block h1 {\n  font-weight: 600;\n}\n\n.article-header img {\n  display: block;\n  position: relative;\n  width: 100%;\n  max-width: 50%;\n}\n\n.article-header p,\n.article-header h1,\n.article-header span {\n  /*margin-left: 60px;*/\n  float: right;\n}\n\n.article-header p {\n  font-size: 1.25rem;\n  line-height: 2rem;\n}\n\n/*  This is that edge case on actual post pages that should prevent giant headlines\nfrom ruining the design. */\n.post-full .article-header .text-block {\n  position: relative;\n  float: right;\n}\n.post-full .article-header img {\n  float: left;\n}\n\n/* TYPOGRAPHY AND SIZING AND STUFF */\n.type-block {\n  font-size: 1.25rem; /* .f4 */\n  line-height: 2rem;\n  display: block;\n  position: relative;\n  clear: both;\n}\n\n.type-block h1 {\n  font-size: 6rem; /* .headline */\n  line-height: 6rem;\n  font-weight: 600;\n}\n\n.type-block h2 {\n  font-size: 4rem; /* .headline */\n  line-height: 4rem;\n  font-weight: 600;\n}\n\n.type-block h3 {\n  font-size: 2rem; /* .headline */\n  line-height: 2rem;\n  font-weight: 500;\n}\n\n.type-block > p:first-child {\n  font-size: 2.25rem; /* .f2 */\n  line-height: 2.5rem;\n  font-weight: 500;\n}\n\n.type-block img {\n  display: block;\n  position: relative;\n  width: 100%;\n  max-width: 50%;\n  float: left;\n  margin-bottom: 2rem;\n}\n\n/*\n  Blocked Links\n*/\n.image-link {\n  border: none;\n}\n.content-link {\n  text-decoration: none;\n}\n\n/*\n  post-Card Stuff\n*/\n.post-card {\n  margin-bottom: 6rem;\n}\n\n/*\n  Type Stuff\n*/\n.article-header {\n  margin-bottom: 2rem;\n}\n\n/* some brief link stuff */\na:hover {\n  text-decoration: none;\n}\n\n.type-block p,\n.type-block ul,\n.type-block ol,\n.type-block blockquote,\n.type-block h1,\n.type-block h2,\n.type-block h3,\n.type-block h4,\n.type-block h5,\n.type-block h6 {\n  margin-top: 0;\n  margin-bottom: 3rem;\n}\n\n.type-block > p,\n.type-block > ul,\n.type-block > ol,\n.type-block > blockquote,\n.type-block > h1,\n.type-block > h2,\n.type-block > h3,\n.type-block > h4,\n.type-block > h5,\n.type-block > h6,\n.type-block pre,\n.type-block div.gist,\n.kg-card-markdown > p,\n.kg-card-markdown > ul,\n.kg-card-markdown > ol,\n.kg-card-markdown > blockquote,\n.kg-card-markdown > h1,\n.kg-card-markdown > h2,\n.kg-card-markdown > h3,\n.kg-card-markdown > h4,\n.kg-card-markdown > h5,\n.kg-card-markdown > h6,\n.kg-card-markdown pre,\n.kg-card-markdown div.gist,\n.footnotes {\n  margin-left: calc(50% + 60px);\n}\n\n.type-block img + p:after {\n  content: \"\";\n  display: block;\n  clear: both;\n}\n\n.type-block a,\n.kg-card-markdown a {\n  color: #198eff;\n  font-weight: 600;\n  text-decoration: none;\n}\n\n.type-block a:hover,\n.kg-card-markdown a:hover {\n  text-decoration: underline;\n}\n\nhr {\n  border: 2px solid #1b1b1b;\n  margin-bottom: 3rem;\n  margin-top: 3rem;\n  margin-left: calc(50% + 60px);\n}\n\n/*\n  Navigation\n*/\n.site-header {\n  margin-bottom: 2rem;\n  clear: both;\n  position: relative;\n  display: inline-block;\n  width: 100%;\n}\n.header-navigation ul.nav,\n.type-block ul.nav {\n  list-style: none;\n  margin: 0;\n  padding: 0;\n}\n.header-navigation ul.nav li {\n  display: inline-block;\n  float: left;\n  /*margin-right: 1rem;*/\n  margin-left: 2rem;\n  font-size: 1.5rem;\n  line-height: 2rem;\n  font-weight: 500;\n  text-transform: uppercase;\n}\n.type-block ul.nav li {\n  margin-left: calc(50% + 60px);\n  font-weight: 500;\n}\n.header-navigation ul.nav li a,\n.type-block ul.nav li a {\n  text-decoration: none;\n}\n.header-navigation ul.nav li a:hover,\n.type-block ul.nav li a:hover {\n  text-decoration: underline;\n}\n\n.site-logo {\n  max-height: 40px;\n  float: right;\n  margin-right: 2rem;\n}\n\n/*\n  subscribe-form\n*/\n.subscribe-form {\n  width: calc(100% - 1rem);\n  margin-left: 0.5rem;\n  padding: 1.5rem;\n  border: 4px solid #eef3f4;\n  background-color: #1b1b1b;\n  width: calc(50% - 60px);\n  float: right;\n  clear: both;\n  position: inline-block;\n  display: block;\n}\n.subscribe-form * {\n  color: white;\n}\n.subscribe-form .form-group input {\n  width: 100%;\n  border: none;\n  padding: 3px 10px;\n  font-size: 18px;\n  line-height: 24px;\n  height: 36px;\n  margin-bottom: 1rem;\n  color: black;\n  font-family: \"ManifoldCF\";\n  font-weight: 500;\n  color: #1b1b1b;\n  -webkit-font-smoothing: antialiased;\n  font-smoothing: antialiased;\n}\n\n.subscribe-form button {\n  margin: 1rem auto 2rem;\n  display: block;\n  padding: 5px 20px;\n  color: #1b1b1b;\n  background-color: white;\n  border: none;\n  cursor: pointer;\n  font-size: 1.5rem;\n  font-weight: 600;\n  text-transform: uppercase;\n}\n.subscribe-form button:hover {\n  text-decoration: underline;\n}\n.subscribe-form button span {\n  color: #1b1b1b;\n}\n\n/*\n  .pagination\n*/\n.pagination {\n  display: block;\n  margin: 2rem auto;\n  width: 100%;\n  text-align: center;\n}\n\n/*\n  .footer\n*/\n.site-footer {\n  display: block;\n  width: 100%;\n  background: black;\n  padding: 2rem;\n}\n\n.site-footer p,\n.site-footer h1,\n.site-footer h2,\n.site-footer h3 {\n  color: white;\n}\n\n/* Middling */\n@media screen and (max-width: 1100px) {\n}\n\n/* small */\n@media screen and (max-width: 700px) {\n  .post-card {\n    margin-bottom: 4rem;\n  }\n\n  .article-header {\n    max-height: none; /* We may need this again. */\n  }\n\n  .article-header .text-block,\n  .text-block,\n  .type-block {\n    position: relative;\n    width: calc(100% - 4rem);\n    margin: 0px auto;\n  }\n  .pagination {\n    margin: 2rem auto;\n  }\n\n  .post-full .article-header .text-block {\n    float: none;\n  }\n\n  .subscribe-form {\n    width: calc(100% - 4rem);\n    float: none;\n    margin: 0px auto;\n  }\n\n  .type-block img {\n    float: none;\n    margin-left: 0rem;\n    max-width: 100%;\n  }\n\n  .article-header img {\n    float: none;\n    margin-left: 2rem;\n    max-width: calc(100% - 4rem);\n    margin-bottom: 2rem;\n  }\n\n  .type-block p,\n  .type-block ul,\n  .type-block ol,\n  .type-block blockquote,\n  .type-block h1,\n  .type-block h2,\n  .type-block h3,\n  .type-block h4,\n  .type-block h5,\n  .type-block h6,\n  .type-block hr,\n  .type-block pre,\n  .type-block div.gist,\n  .type-block ul.nav li {\n    margin-left: 0px;\n  }\n}\n\n/*\n  Utility Classes\n  To begin moving towards low specificity classes to control the layout I'll add\n  utility classes that can be used to replicate these behaviours.\n*/\n\n/*\n  Sorted Type\n  All content Type hugs the right of the screen except on mobile.\n  These classes let that be a reality for any element.\n */\n.sorted-type,\n.sr {\n  margin-left: calc(50% + 60px);\n}\n@media screen and (max-width: 700px) {\n  .sorted-type,\n  .sr {\n    margin-left: 0px;\n  }\n}\n"],"names":[],"mappings":"AAAA,gBAAgB;AAChB;;;;EAIE;AACF,UAAU;EACR,WAAW,EAAE,YAAY;EACzB,GAAG,EAAE,oDAAoD;EAAE,qBAAqB;EAChF,GAAG,EAAE,2DAA2D,CAC5D,2BAA2B,EAC7B,sDAAsD,CAAC,eAAe,EACtE,qDAAqD,CAAC,cAAc,EACpE,oDAAoD,CAAC,kBAAkB;EAAE,0BAA0B;EACrG,WAAW,EAAE,GAAG;EAChB,UAAU,EAAE,MAAM;;AAEpB,UAAU;EACR,WAAW,EAAE,YAAY;EACzB,GAAG,EAAE,2DAA2D;EAAE,qBAAqB;EACvF,GAAG,EAAE,kEAAkE,CACnE,