"GreycliffCF\";\n  src: url(\"/assets/fonts/greycliff/GreycliffCF-MediumOblique.eot\"); /* IE9 Compat Mode */\n  src: url(\"/assets/fonts/greycliff/GreycliffCF-MediumOblique.eot?#iefix\")\n      format(\"embedded-opentype\"),\n    url(\"/assets/fonts/greycliff/GreycliffCF-MediumOblique.woff2\")\n      format(\"woff2\"),\n    url(\"/assets/fonts/greycliff/GreycliffCF-MediumOblique.woff\") format(\"woff\"),\n    url(\"/assets/fonts/greycliff/GreycliffCF-MediumOblique.ttf\")\n      format(\"truetype\"); /* Safari, Android, iOS */\n  font-weight: 500;\n  font-style: italic;\n}\n@font-face {\n  font-family: \"GreycliffCF\";\n  src: url(\"/assets/fonts/greycliff/GreycliffCF-DemiBold.eot\"); /* IE9 Compat Mode */\n  src: url(\"/assets/fonts/greycliff/GreycliffCF-DemiBold.eot?#iefix\")\n      format(\"embedded-opentype\"),\n    url(\"/assets/fonts/greycliff/GreycliffCF-DemiBold.woff2\") format(\"woff2\"),\n    url(\"/assets/fonts/greycliff/GreycliffCF-DemiBold.woff\") format(\"woff\"),\n    url(\"/assets/fonts/greycliff/GreycliffCF-DemiBold.ttf\") format(\"truetype\"); /* Safari, Android, iOS */\n  font-weight: 600;\n  font-style: normal;\n}\n@font-face {\n  font-family: \"GreycliffCF\";\n  src: url(\"/assets/fonts/greycliff/GreycliffCF-DemiBoldOblique.eot\"); /* IE9 Compat Mode */\n  src: url(\"/assets/fonts/greycliff/GreycliffCF-DemiBoldOblique.eot?#iefix\")\n      format(\"embedded-opentype\"),\n    url(\"/assets/fonts/greycliff/GreycliffCF-DemiBoldOblique.woff2\")\n      format(\"woff2\"),\n    url(\"/assets/fonts/greycliff/GreycliffCF-DemiBoldOblique.woff\") format(\"woff\"),\n    url(\"/assets/fonts/greycliff/GreycliffCF-DemiBoldOblique.ttf\")\n      format(\"truetype\"); /* Safari, Android, iOS */\n  font-weight: 600;\n  font-style: italic;\n}\n@font-face {\n  font-family: \"GreycliffCF\";\n  src: url(\"/assets/fonts/greycliff/GreycliffCF-Bold.eot\"); /* IE9 Compat Mode */\n  src: url(\"/assets/fonts/greycliff/GreycliffCF-Bold.eot?#iefix\")\n      format(\"embedded-opentype\"),\n    url(\"/assets/fonts/greycliff/GreycliffCF-Bold.woff2\") format(\"woff2\"),\n    url(\"/assets/fonts/greycliff/GreycliffCF-Bold.woff\") format(\"woff\"),\n    url(\"/assets/fonts/greycliff/GreycliffCF-Bold.ttf\") format(\"truetype\"); /* Safari, Android, iOS */\n  font-weight: 700;\n  font-style: normal;\n}\n@font-face {\n  font-family: \"GreycliffCF\";\n  src: url(\"/assets/fonts/greycliff/GreycliffCF-BoldOblique.eot\"); /* IE9 Compat Mode */\n  src: url(\"/assets/fonts/greycliff/GreycliffCF-BoldOblique.eot?#iefix\")\n      format(\"embedded-opentype\"),\n    url(\"/assets/fonts/greycliff/GreycliffCF-BoldOblique.woff2\") format(\"woff2\"),\n    url(\"/assets/fonts/greycliff/GreycliffCF-BoldOblique.woff\") format(\"woff\"),\n    url(\"/assets/fonts/greycliff/GreycliffCF-BoldOblique.ttf\") format(\"truetype\"); /* Safari, Android, iOS */\n  font-weight: 700;\n  font-style: italic;\n}\n\n\n/* font converted using font-converter.net. thank you! */\n@font-face {\n  font-family: \"PlayfairDisplay-Black\";\n  src: url(\"/assets/fonts/playfair/PlayfairDisplay-Black.eot\"); /* IE9 Compat Modes */\n  src: url(\"/assets/fonts/playfair/PlayfairDisplay-Black.eot?#iefix\") format(\"embedded-opentype\"), /* IE6-IE8 */\n    url(\"/assets/fonts/playfair/PlayfairDisplay-Black.otf\") format(\"opentype\"), /* Open Type Font */\n    url(\"/assets/fonts/playfair/PlayfairDisplay-Black.svg\") format(\"svg\"), /* Legacy iOS */\n    url(\"/assets/fonts/playfair/PlayfairDisplay-Black.ttf\") format(\"truetype\"), /* Safari, Android, iOS */\n    url(\"/assets/fonts/playfair/PlayfairDisplay-Black.woff\") format(\"woff\"), /* Modern Browsers */\n    url(\"/assets/fonts/playfair/PlayfairDisplay-Black.woff2\") format(\"woff2\"); /* Modern Browsers */\n  font-weight: normal;\n  font-style: normal;\n}\n/* font converted using font-converter.net. thank you! */\n@font-face {\n  font-family: \"PlayfairDisplay-Black\";\n  src: url(\"/assets/fonts/playfair/PlayfairDisplay-BlackItalic.eot\"); /* IE9 Compat Modes */\n  src: url(\"/assets/fonts/playfair/PlayfairDisplay-BlackItalic.eot?#iefix\") format(\"embedded-opentype\"), /* IE6-IE8 */\n    url(\"/assets/fonts/playfair/PlayfairDisplay-BlackItalic.otf\") format(\"opentype\"), /* Open Type Font */\n    url(\"/assets/fonts/playfair/PlayfairDisplay-BlackItalic.svg\") format(\"svg\"), /* Legacy iOS */\n    url(\"/assets/fonts/playfair/PlayfairDisplay-BlackItalic.ttf\") format(\"truetype\"), /* Safari, Android, iOS */\n    url(\"/assets/fonts/playfair/PlayfairDisplay-BlackItalic.woff\") format(\"woff\"), /* Modern Browsers */\n    url(\"/assets/fonts/playfair/PlayfairDisplay-BlackItalic.woff2\") format(\"woff2\"); /* Modern Browsers */\n  font-weight: normal;\n  font-style: italic;\n}\n@font-face {\n  font-family: \"Futura\";\n  src: url(\"/assets/fonts/futura/futura.eot\"); /* IE9 Compat Modes */\n  src: url(\"/assets/fonts/futura/futura.eot?#iefix\") format(\"embedded-opentype\"), /* IE6-IE8 */\n    url(\"/assets/fonts/futura/futura.otf\") format(\"opentype\"), /* Open Type Font */\n    url(\"/assets/fonts/futura/futura.svg\") format(\"svg\"), /* Legacy iOS */\n    url(\"/assets/fonts/futura/futura.ttf\") format(\"truetype\"), /* Safari, Android, iOS */\n    url(\"/assets/fonts/futura/futura.woff\") format(\"woff\"), /* Modern Browsers */\n    url(\"/assets/fonts/futura/futura.woff2\") format(\"woff2\"); /* Modern Browsers */\n  font-weight: normal;\n  font-style: normal;\n}\n\n/*\n  Colors\n*/\n:root {\n  --yellow-bg: #FFFFF6;\n  --mustard: #FFC729;\n  --electric-green: #00C764;\n  --off-black: #333333;\n  --off-grey: #747474;\n  --neon-pink: #FF008D;\n  --light-grey: #D8D8D8;\n}\n\n/*\n  Portfolio Grid\n\n  There is a new grid for the Portfolio and I think it's gonna be fantastic.\n  6 columns, 16.666667% each\n*/\n.supercontainer {\n  padding:0px;\n  width:100%;\n  margin:auto;\n  height:inherit;\n  display:block;\n  position:relative;\n  min-height: 100vh;\n  overflow:hidden;\n}\n/* This is the supe