/*
  Portfolio Grid

  There is a new grid for the Portfolio and I think it's gonna be fantastic.
  6 columns, 16.666667% each
*/
.supercontainer {
  padding: 0px;
  width: 100%;
  margin: auto;
  height: inherit;
  display: block;
  position: relative;
  min-height: 100vh;
  overflow: hidden; }

/* This is the super super container, the one that is absolutely the biggest size. */
.supercontainer.super {
  max-width: 1600px;
  box-shadow: 0px 0px 20px 0px rgba(0, 0, 0, 0.23); }

.supercontainer.yellow-bg, html {
  background-color: var(--yellow-bg); }

.supercontainer.white-bg, html {
  background-color: white; }

.new-grid-container, .ng-c {
  display: block;
  position: relative;
  width: 100%;
  max-width: 1152px;
  margin: 0px auto; }

.new-grid-1-column, .ng-1c {
  display: block;
  position: relative;
  width: 100%;
  max-width: 16.66666%;
  box-sizing: border-box;
  padding: 0px 32px; }

.new-grid-2-column, .ng-2c {
  display: block;
  position: relative;
  width: 100%;
  max-width: 33.33333%;
  box-sizing: border-box;
  padding: 0px 32px; }

.new-grid-3-column, .ng-3c {
  display: block;
  position: relative;
  width: 100%;
  max-width: 50%;
  box-sizing: border-box;
  padding: 0px 32px; }

.new-grid-4-column, .ng-4c {
  display: block;
  position: relative;
  width: 100%;
  max-width: 66.66666%;
  box-sizing: border-box;
  padding: 0px 32px; }

.new-grid-5-column, .ng-5c {
  display: block;
  position: relative;
  width: 100%;
  max-width: 83.33333%;
  box-sizing: border-box;
  padding: 0px 32px; }

.new-grid-6-column, .ng-6c {
  display: block;
  position: relative;
  width: 100%;
  max-width: 100%;
  box-sizing: border-box;
  padding: 0px 32px; }

@media (--breakpoint-new-not-small) {
  /* -ns */
